				   success: (res) => {
					   console.log(res)
					if(res.statusCode==200){
						console.log((res.data.start1+res.data.end)>res.data.start)
						if((res.data.start1+res.data.end)>res.data.start){
							fl=false
						}else{
							fl=true
						}
						
						
						if(fl){
							uni.showModal({
								title:'温馨提示',
								content:'您不是商家会员不可以上传商品,是否要去充值商家会员',
