									})
								}
									
							}else{
								
								console.log(this.imgroup1upData.length>=2&&this.name!=""&&this.mj!=null&&this.price!=""&&this.xinxi!=""&&this.imgroup7upData.length>0&&this.yunFei2!=null)
								
